import React, { useMemo, useState } from "react";
import SpinWheelBg from "../../assets/images/spin/behind.png";
import SpinWheelMain from "../../assets/images/spin/spin.png";
import SpinWheelArrow from "../../assets/images/spin/mid.png";
import SpinWheelBottomLeft from "../../assets/images/spin/bottom-left.png";
import SpinWheelBottomRight from "../../assets/images/spin/bottom-right.png";
import styled, { keyframes } from "styled-components";
import "./style.scss";

export default function Spin() {
  const [randomDeg, setRandomDeg] = useState(0);

  const rotate = keyframes`
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(${1080 + randomDeg}deg);
  }
`;

  const Rotate = styled.img`
    animation: ${rotate} 3s linear;
  `;
  const getRandomInt = (max) => Math.floor(Math.random() * max) + 1;

  const styles = {
    transform: `rotate(${360 + randomDeg}deg)`,
  };

  return useMemo(
    () => (
      <div className="spin">
        <div className="spin-intro">
          <h4 className="spin-title-small animate__animated animate__bounce animate__repeat-2">
            PLAY MORE TO EARN MORE
          </h4>
          <h2 className="spin-title animate__animated animate__fadeInUp">
            SPIN TO EARN
          </h2>
        </div>
        <div className="spin-wheel">
          <div className="spin-wheel-wrapper">
            <div className="spin-wheel-bg">
              <img src={SpinWheelBg} alt="spin-wheel-bg" />
            </div>
            <div className="spin-wheel-main">
              <Rotate src={SpinWheelMain} style={styles} />
            </div>
            <div className="spin-wheel-arrow">
              <img src={SpinWheelArrow} />
            </div>
            <div className="spin-wheel-bottom-left">
              <img src={SpinWheelBottomLeft} />
            </div>
            <div className="spin-wheel-bottom-right">
              <img src={SpinWheelBottomRight} />
            </div>
            <button
              className="btn-primary spin-btn"
              onClick={() => {
                setRandomDeg(getRandomInt(360));
              }}
            >
              <div className="light-blur">
                <div className="btn-primary-wrapper ">SPIN NOW</div>
              </div>
            </button>
          </div>
        </div>
      </div>
    ),
    [randomDeg]
  );
}
